@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/FiraSans-Medium.woff2") format("woff2"), url("../fonts/FiraSans-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/FiraSans-Bold.woff2") format("woff2"), url("../fonts/FiraSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html,
body {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-arrow::-webkit-outer-spin-button,
.no-arrow::-webkit-inner-spin-button {
  appearance: none; }

.no-arrow {
  appearance: textfield; }

.page-header {
  padding: 0 calc(50% - 683px); }

.page-header__wrapper {
  background-repeat: no-repeat;
  background-position: center;
  background-color: #c4c4c4;
  background-image: url("../img/bg-promo-desktop@1x.jpg");
  min-height: 906px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header__wrapper {
      background-image: url("../img/bg-promo-desktop@2x.jpg"); } }

.page-header__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 450px;
  padding-top: 32px;
  padding-left: 110px;
  padding-right: 114px; }

.page-header_logo img {
  width: 137px;
  height: 48px; }

.page-header__top a {
  margin-right: 15px; }
  .page-header__top a:last-child {
    margin-right: 0; }

.page-header__phone,
.page-header__order {
  text-decoration: none;
  color: #484848;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px; }
  .page-header__phone:hover, .page-header__phone:focus,
  .page-header__order:hover,
  .page-header__order:focus {
    color: #1fc0fc; }
  .page-header__phone:active,
  .page-header__order:active {
    opacity: 0.7; }

.page-header__order:hover,
.page-header__order:focus {
  border-color: #1fc0fc; }

.page-header__order {
  text-transform: uppercase;
  border-bottom: 1px dashed #484848; }

.page-header__promo {
  max-width: 360px;
  margin: 0 auto;
  margin-bottom: 32px;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: white;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }

.page-header__scroll {
  display: block;
  width: 76px;
  height: 137.5px;
  border: none;
  margin: 0 auto;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/bg-promo-scroll-desktop@1x.png");
  transition: transform 0.3s ease-in-out; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header__scroll {
      background-image: url("../img/bg-promo-scroll-desktop@2x.png"); } }
  .page-header__scroll:hover, .page-header__scroll:focus {
    transform: scale(1.1); }
  .page-header__scroll:active {
    transform: scale(0.9); }

@media (max-width: 1023px) {
  .page-header {
    padding: 0 calc(50% - 511.5px); }
  .page-header__wrapper {
    background-size: cover; } }

@media (max-width: 767px) {
  .page-header__wrapper {
    background-image: url("../img/bg-promo-mobile@1x.jpg");
    min-height: 605px; } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header__wrapper {
      background-image: url("../img/bg-promo-mobile@1x.jpg"); } }

@media (max-width: 767px) {
  .page-header__top {
    margin-bottom: 272px;
    padding-top: 21px;
    padding-left: 0;
    padding-right: 6px; }
  .page-header_logo {
    order: -1;
    margin-left: 17px; }
  .page-header_logo img {
    width: 120px;
    height: 42px; }
  .page-header__phone,
  .page-header__order {
    font-size: 14px;
    line-height: 16.8px; }
  .page-header__order {
    margin-left: auto;
    margin-right: 6px; } }

.body {
  min-width: 320px;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #484848;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal; }
